@import url("https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,500;0,600;1,300;1,400&display=swap");

* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

:root {
  --color-bg: #fffafa;
  --color-bg-variant: #2c2c6c;
  --color-primary: #4db5ff;
  --color-primary-variant: rgba(77, 181, 255, 0.4);
  --color-black: black;
  --color-light: rgba(255, 255, 255, 0.6);

  /* cnahges these for your owmn colors */

  --transition: all 400ms ease;

  --container-width-lg: 75%;
  --container-width-md: 86%;
  --container-width-ms: 90%;
  /* keep these for media queryies */
}

html {
  scroll-behavior: smooth;
}

::-webkit-scrollbar {
  display: none;
}

body {
  font-family: "Nunito", sans-serif;
  background: var(--color-bg);
  color: var(--color-black);
  line-height: 1.7;
}
/* general styles */

.container {
  width: var(--container-width-lg);
  margin: 0 auto;
}

h1,
h2,
h3,
h4,
h5 {
  font-weight: 500;
}

h1 {
  font-size: 2.5rem;
}

section > h2,
section > h5 {
  text-align: center;
  color: var(--color-black);
}

section > h2 {
  color: var(--color-primary);
  margin-bottom: 3rem;
}

.text-light {
  color: var(--color-black);
}

a {
  color: var(--color-primary);
  transition: var(--transition);
}

.btn {
  background: gray;
  width: max-content;
  display: inline-block;
  color: var(--color-bg);
  padding: 0.75rem 1.2rem;
  border-radius: 0.4rem;
  cursor: pointer;
  border: 1px solid var(--color-black);
  transition: var(--transition);
}

.btn:hover {
  background: white;
  color: var(--color-black);
  border-color: transparent;
}

.btn-primary {
  background: gray;
  color: var(--color-bg);
}

img {
  display: block;
  width: 100%;
  object-fit: cover;
}

/* media queries (tablets) */

@media screen and (max-width: 1024px) {
  .container {
    width: var(--container-width-md);
  }
  section {
    margin-top: 6rem;
  }
}

/* media queries (phones) */

@media screen and (max-width: 600px) {
  .container {
    width: var(--container-width-ms);
  }
  section > h2 {
    margin-bottom: 2rem;
  }
}
